import React, {useEffect, useState,useRef} from 'react';

import MyButton from './UI/button/MyButton.js';
import MyTextArea from './UI/textArea/MyTextArea.js';
import MyInput from './UI/input/MyInput.js';
import MySelect from './UI/select/MySelect.js';


const PostForm = ({create,editPost,save,post,setPost,filter,setFilter,allNotes,setAllNotes}) => {

  // const [post, setPost] = useState({title:'',body:''});

 const [selectedCategory, setSelectedCategory] = useState('apple');


// console.log(allNotes[0].selectedCategoriesArray)
// .selectCategories[0]

  const addNewPost = (e) => {
        console.log(post)

    e.preventDefault()
    const newPost = {
            ...post, id: Date.now()
        }
        create(newPost)
        console.log(save)
        console.log(post)


        // allNotes[0].selectCategories[0];
        console.log(allNotes[0].selectedCategoriesArray)
      
     allNotes[0].selectedCategoriesArray.push(post.body) 
        console.log(allNotes[0].selectedCategoriesArray)
      
  }

   const saveEditedPost = (e) => {
    e.preventDefault()
    const editedPost = {
            ...post
        }
        editPost(editedPost)
        console.log(save)
        // console.log(create)

  }



    return (
    <div className="PostForm">
            <form>

          <br/>
          <br/>


название продукта :
          <MyInput
            // ref={bodyInputRef}
            value={post.title}
            onChange={e=>setPost({ ...post, title: e.target.value})}
           type='text' 
           placeholder='placeholder1' 
           />

          <br/>
          <br/>












выбрать категорию продукта  из списка ранее использованных  категорий :
          <select
            value={selectedCategory}
            onChange={event => (setSelectedCategory(event.target.value),
                        setPost({ ...post, body: event.target.value})        
                )}
            >
            <option disabled value="">{selectedCategory}</option>
            {allNotes[0].selectedCategoriesArray.map((option,index) =>
                <option key={index} value={option.index}>
                    {option}
                </option>
            )}
        </select>

          <br/>
















написать категорию продукта :
          <MyTextArea type='text' 
            value={post.body}
            // onChange={e=>setBody(e.target.value)}
            onChange={e=>setPost({ ...post, body: e.target.value})}

            placeholder='placeholder2' 
          />
          
          <br/>

          ценa :
          <MyInput
            // ref={bodyInputRef}
            value={post.price}
            onChange={e=>setPost({ ...post, price: e.target.value})}
           type='text' 
           placeholder='placeholder3' 
           />

          <br/>

          <MyButton 
            onClick={ save ? addNewPost : saveEditedPost
            }
            // onClick={addNewPost}
          >
          button create post
          </MyButton>

        </form>
    </div>


    );
};

export default PostForm;