    import React, {useEffect, useState,useRef} from 'react';

// import {useHistory} from 'react-router-dom';
// import MyButton from "./UI/button/MyButton";
import PostItem from "./PostItem";
import MyButton from './UI/button/MyButton.js';
import MyInput from './UI/input/MyInput.js';


const ProductsListSpecificCategoty = ({posts,title,getPricesSum,allNotes,setAllNotes}) => {
// console.log(posts)

    // if (!posts.length) {
    //     return (
    //         <h1 style={{textAlign: 'center'}}>
    //             Посты не найдены!
    //         </h1>
    //     )
    // }
    const [productCategory,setProductCategory] = useState(allNotes[0].selectedCategoriesArray[1]);


// console.log(allNotes);


function getSumProductsSpecificCategoty(arr,category){

    let resArr = [] 

    let productsForPeriodArr = arr.forEach(
     function(elem){ 

        elem.notes.forEach( (elem) => {
             if( elem.body===category){
               resArr.push(elem)
               // console.log(elem,...elem.notes)
                // console.log(resArr)
            }
          }
        )
      }


    )      
   
        
    // console.log(resArr)
    return resArr

}

// getProducnsForPeriod(allNotes)

    // console.log(Number.isInteger(1))


    return (<>


      <br/>
      <br/>
      <br/>

      <h1 className='info'>ProductsListSpecificCategoty</h1>


      <div className="PostForm">

     

        <MyInput
            // ref={bodyInputRef}
            value={productCategory}
            onChange={e=>setProductCategory(e.target.value)}
            type='text' 
            placeholder='productCategory' 
       /> 

      

            </div>


            <div className="product-table">


            <table>
            <thead>
            <tr>
            <th>название продукта</th>
            <th>категорию продукта</th>
            <th>цену</th>

            </tr>
            </thead>

            <tbody>

            {getSumProductsSpecificCategoty(allNotes,productCategory).map((post, index) =>(
              <tr key={index}>
              <td>
              {post.title}
              </td>
              <td>
              {post.body}
              </td> 
              <td>
              {post.price}
              </td>

             
              </tr>

              ))}

            <tr style={{textAlign:'right'}}>
            <td  >
            суммарная стоимость

            </td>
            <td>
            </td> 

            <td>
              {
                getPricesSum(getSumProductsSpecificCategoty(allNotes,productCategory))==NaN ?
                 '-'
                 :
                 getPricesSum(getSumProductsSpecificCategoty(allNotes,productCategory))
                }

            </td>
            
            </tr>




            </tbody>

            </table>


            <br/>
            <br/>
            <br/>

            {}
            
            

            
            </div>



            </>)}



    export default ProductsListSpecificCategoty ;