    // </h1>

import React from 'react';
// import {useHistory} from 'react-router-dom';
// import MyButton from "./UI/button/MyButton";
import PostItem from "./PostItem";
import MyButton from './UI/button/MyButton.js';


const ProductTable = ({posts,title,remove,open,getPricesSum}) => {
// console.log(posts)

    // if (!posts.length) {
    //     return (
    //         <h1 style={{textAlign: 'center'}}>
    //             Посты не найдены!
    //         </h1>
    //     )
    // }

    

    return (

    <div className="product-table">

      <h1 className='info'>ProductTable</h1>

       <table>
         <thead>
            <tr>
                <th>название продукта</th>
                <th>категорию продукта</th>
                <th>цену</th>
               
              </tr>
         </thead>

          <tbody>

            {posts.map((post, index) =>(
                <tr key={index}>
                      <td>
                          {post.title}
                         </td>
                         <td>
                          {post.body}
                         </td> 
                          <td>
                          {post.price}
                         </td>

                          <td>
                          <MyButton onClick={() => open(post)}>
                    Открыть
                </MyButton>

                         </td>
                         <td>
                         <MyButton onClick={() => remove(post)}>
                         Удалить
                         </MyButton>
                         </td>
                </tr>

                         ))}

                <tr style={{textAlign:'right'}}>
                      <td  >
                            суммарная стоимость
                      
                        </td>
                         <td>
                        </td> 
                        <td>
                        {getPricesSum(posts)}
                        </td>
                 </tr>




            </tbody>

            </table>
            </div>



     )}



export default ProductTable ;